.main-c {
  width: 100%;
  max-height: 500px;
}
.inner-c.title-nd-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.options {
  cursor: pointer;
}
.inner-c.title-nd-options .title h2 {
  padding-top: 2px;
  font-size: 17px;
  font-family: sans-serif;
}
.graph-data {
  width: 100%;
  height: 250px;
  border: 1px solid #000;
  margin-top: 20px;
}
.LoadingText {
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  margin-top: 20%;
}
